

































































































.weightDetail{
  text-align: left;
  height: 100%;
  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    .queryBtns {
      float: right;
      margin-top: 14px;
    }
    .search-title {
      &:before {
        content: '';
        display: inline-block;
        width: 18px;
        height: 18px;
        background: url("../../../assets/redLine/sprite.png") 0 -63px no-repeat;
        background-size: auto;
        vertical-align: middle;
        margin-right: 8px;
      }
    }
  }
}
